//
//  _sweetalert2.scss
//

.swal2-container {
  .swal2-title {
    padding: 24px 24px 0;
    font-size: var(--#{$prefix}font-3xl);
    font-weight: $font-weight-medium;
  }
}

.swal2-popup {
  padding-bottom: 24px;
  border-radius: $modal-content-border-radius;
  background-color: var(--#{$prefix}secondary-bg);
  color: var(--#{$prefix}body-color);

  .swal2-title {
    color: var(--#{$prefix}heading-color);
  }

  .swal2-html-container {
    color: var(--#{$prefix}body-color);
  }
}

.swal2-footer {
  border-top: 1px solid var(--#{$prefix}border-color);
  color: var(--#{$prefix}body-color);
}

.swal2-html-container {
  font-size: var(--#{$prefix}font-lg);
}

.swal2-icon {
  &.swal2-question {
    border-color: var(--#{$prefix}info);
    color: var(--#{$prefix}info);
  }

  &.swal2-success {
    [class^=swal2-success-line] {
      background-color: var(--#{$prefix}success);
    }

    .swal2-success-ring {
      border-color: rgba(var(--#{$prefix}success-rgb), 0.3);
    }
  }

  &.swal2-warning {
    border-color: var(--#{$prefix}warning);
    color: var(--#{$prefix}warning);
  }
}

.swal2-styled {
  &:focus {
    box-shadow: none;
  }
}

.swal2-loader {
  border-color: var(--#{$prefix}primary) transparent var(--#{$prefix}primary) transparent;
}

.swal2-timer-progress-bar {
  background-color: rgba(var(--#{$prefix}success-rgb), 0.4);
}

.swal2-progress-steps {
  .swal2-progress-step {
    background: var(--#{$prefix}primary);

    &.swal2-active-progress-step {
      background: var(--#{$prefix}primary);

      &~.swal2-progress-step,
      &~.swal2-progress-step-line {
        background: rgba(var(--#{$prefix}primary-rgb), 0.3);
      }
    }
  }

  .swal2-progress-step-line {
    background: var(--#{$prefix}primary);
  }
}

.swal2-actions.swal2-loading {
  .swal2-styled.swal2-confirm {
    border-left-color: var(--#{$prefix}primary) !important;
    border-right-color: var(--#{$prefix}primary) !important;
  }
}

:is(.swal2-file, .swal2-input, .swal2-textarea) {
  border: 1px solid $input-border-color;

  &:focus {
    box-shadow: none;
    border-color: $input-focus-border-color;
  }
}

.swal2-input {
  height: auto;
  display: block;
  padding: $input-padding-y $input-padding-x;
  font-family: $input-font-family;
  @include font-size($input-font-size);
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  background-color: $input-bg;
  background-clip: padding-box;
  border: $input-border-width solid $input-border-color;
}

.swal2-close {
  font-family: $font-family-base;
  font-weight: $font-weight-light;
  font-size: 28px;

  &:focus {
    box-shadow: none;
  }

  &:hover {
    color: var(--#{$prefix}primary);
  }
}

.swal2-validation-message {
  background-color: transparent;
}